.vision-container {
  max-width: 1200px;
  margin: auto;
  padding: 40px 20px;
  text-align: center;
}

/* Vision Section */
.vision-div {
  background: linear-gradient(135deg, #e3f2fd, #ffffff);
  padding: 40px;
  border-radius: 15px;
  margin-bottom: 40px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

.vision-div:hover {
  transform: translateY(-5px);
}

.vision-div h1 {
  font-size: 30px;
  color: #002d62;
  margin-bottom: 15px;
  font-weight: bold;
}

.vision-div p {
  font-size: 20px;
  color: #444;
  line-height: 1.8;
  max-width: 800px;
  margin: auto;
}

.mission-txt {
  font-size: 30px;
  color: #002d62;
  margin-bottom: 20px;
  font-weight: bold;
}

/* Mission Grid */
.mission-div {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 25px;
  justify-content: center;
  padding: 20px 0;
}

/* Mission Card */
.mission-card {
  background: rgba(255, 255, 255, 0.85);
  padding: 25px;
  border-radius: 15px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  text-align: center;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.mission-card:hover {
  transform: translateY(-7px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  background: rgba(255, 255, 255, 0.95);
}

.mission-card img {
  width: 90px;
  height: 90px;
  object-fit: contain;
  margin-bottom: 15px;
  transition: transform 0.3s ease-in-out;
}

.mission-card:hover img {
  transform: scale(1.1);
}

.mission-card h3 {
  font-size: 20px;
  color: #002d62;
  margin-bottom: 12px;
  font-weight: bold;
}

.mission-card p {
  font-size: 16px;
  color: #555;
  line-height: 1.6;
  max-width: 260px;
  margin: auto;
}
