.custom-card {
  width: 320px; /* Slightly larger for better content fit */
  height: auto;
  background: linear-gradient(135deg, #9aa6b2 20%, #b0bccb 100%);
  border-radius: 12px;
  box-shadow: 6px 6px 20px rgba(154, 166, 178, 0.3);
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  margin-right: 20px;
  padding: 18px;
}

.custom-card:hover {
  transform: translateY(-8px);
  box-shadow: 8px 8px 25px rgba(154, 166, 178, 0.5);
  background: linear-gradient(135deg, #b0bccb 10%, #9aa6b2 90%);
}

.custom-card img {
  border-radius: 12px;
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
}

.custom-card img:hover {
  transform: scale(1.08);
  opacity: 0.95;
}

.doctors-section {
  background: rgba(102, 102, 203, 0.15);
  padding: 40px;
  border-radius: 18px;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.12);
  margin: 80px auto;
}

.card-header {
  padding: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 18px;
}

.card-header h2 {
  font-size: 26px;
  font-weight: 700;
  color: #222;
  text-transform: uppercase;
}

.btn-2 {
  background: linear-gradient(
    135deg,
    rgba(0, 128, 0, 0.7),
    rgba(34, 177, 76, 0.8)
  );
  border: 0;
  padding: 14px 22px;
  border-radius: 22px;
  transition: 0.3s;
  box-shadow: 0px 0px 12px 4px rgba(0, 128, 0, 0.5);
  font-weight: 700;
  font-size: 15px;
  color: white;
  text-transform: uppercase;
}

.btn-2:hover {
  transform: translateY(-6px);
  box-shadow: 0px 0px 18px 6px rgba(0, 128, 0, 0.7);
  background: linear-gradient(
    135deg,
    rgba(34, 177, 76, 0.9),
    rgba(0, 128, 0, 0.9)
  );
}

.doctor-info-2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 18px;
}

.doctor-image-2 {
  width: 230px;
  height: 230px;
  object-fit: cover;
  border-radius: 50%;
  border: 6px solid #fff;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.doctor-image-2:hover {
  transform: scale(1.05);
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.15);
}

.doctor-details-2 {
  line-height: 1.7;
  padding: 28px;
  border-radius: 18px;
  background: linear-gradient(135deg, #9aa6b2, #b0bccb);
  box-shadow: 0px 5px 18px rgba(0, 0, 0, 0.12);
  margin-top: 12px;
}

.doctor-details-2 h3 {
  padding: 12px;
  font-size: 22px;
  color: #222;
  text-align: center;
}

.specialization {
  font-size: 17px;
  color: #444;
  font-weight: 600;
}

.doctor-details-2 p {
  font-size: 15px;
  color: #666;
  margin: 6px 0;
}

.doctor-details-2 p strong {
  color: #222;
}

.section-title {
  margin: 25px 0;
  font-size: 38px;
  font-weight: 700;
  color: #2a2a2a;
  text-align: center;
  text-transform: uppercase;
}

/* Responsive Enhancements */
@media (max-width: 768px) {
  .custom-card {
    width: 270px;
  }

  .btn-2 {
    padding: 12px 18px;
    font-size: 13px;
  }

  .doctor-image-2 {
    width: 190px;
    height: 190px;
  }

  .doctor-details-2 {
    padding: 22px;
  }
}

@media (max-width: 480px) {
  .custom-card {
    width: 92%;
    margin: 12px auto;
  }

  .btn-2 {
    font-size: 14px;
    padding: 12px 22px;
  }

  .doctor-details-2 h3 {
    font-size: 20px;
  }

  .doctor-details-2 p {
    font-size: 13px;
  }

  .doctor-image-2 {
    width: 160px;
    height: 160px;
  }

  .doctor-details-2 {
    padding: 18px;
  }

  .section-title {
    font-size: 30px;
  }
}
